import {
  signInWithFacebook,
  signOutWithFacebook,
} from "@/app/api/auth/actions";
import { auth } from "@/app/api/auth/auth";
import { useRouter } from "next/navigation";

export const SignIn = async () => {
  const session = await auth();

  const router = useRouter();

  return (
    <>
      {session ? (
        <form
          action={async () => {
            // signInWithFacebook();
          }}
        >
          <button type="submit">Signout with Facebook</button>
        </form>
      ) : (
        <form
          action={async () => {
            // signOutWithFacebook();
            router.push("/");
          }}
        >
          <button type="submit">Signin with Facebook</button>
        </form>
      )}
    </>
  );
};
